body {
    margin: 0 !important;
    font-family: "SF Pro Display Regular" !important;
}
.auth-layout-wrapper{
background-image: url("../../../public/assets/img/auth/bg-login.png");
display: flex;
flex-direction: row;
height: 100vh;

}
.auth-layout-left-content-wrapper{
flex: 1;
display: flex;
/* justify-content: center; */
/* align-items: center; */
/* background: bisque; */
}
.auth-layout-left-content-wrapper img {
height: 85%;
position: absolute;
bottom:0;
}

.auth-layout-left-content-form-wrapper{
position: absolute;
top: 20%;
left:12%;
/* text-align: center; */
width:24%;
/* font-family: "SF Pro Display Medium" !important; */

/* margin-top:0; */
/* padding:0; */
}
.auth-layout-left-content-form-title-wrapper{
text-align: center;
line-height: 14px;
}

.auth-layout-left-content-form-title-wrapper h3{
font-weight: normal;
line-height: 15px;
font-size:20px;
}
.auth-layout-left-content-form-title-wrapper h1{
font-family: "SF Pro Display Medium";
font-weight: normal;
font-size: 40px;
}
.auth-layout-left-content-form-input-wrapper{
margin-top: 60px;
}

.auth-layout-left-content-form-input-wrapper label{
margin-left:5px;
}
.auth-layout-left-content-form-input-wrapper input {
width: 100%;
padding: 12px 20px;
margin: 8px 0;
/* display: inline-block; */
border: 1px solid #ccc;
border-radius: 15px;
box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.15);
box-sizing: border-box;
}

.auth-layout-left-content-form-input-wrapper button {
width: 25%;
/* text-align: center; */
background-color: #66B1F2;
color: black;
padding: 14px 20px;
margin: 15px 0;
border: none;
border-radius: 15px;
cursor: pointer;
}

.auth-layout-left-content-form-input-wrapper button:hover {
background-color: #F2BF27;
transition: 0.1s;
}

.auth-layout-left-content-form-input-wrapper a{
position: relative;
left:69%;
text-decoration: none;
font-size: 15px;
line-height: 18px;

/* identical to box height */
text-align: center;

color: #7B9FBF;
}

.auth-layout-right-content-wrapper{
flex: 1;
color: white;
}

.auth-layout-content{
padding: 0 64px;
display: flex;
flex-direction: column;
position: absolute;
top: 20%;
/* justify-content: center; */
/* height: 100%; */
}


/* Responsive  */
@media screen and (max-width: 1146px) {
    .auth-layout-left-content-form-title-wrapper h3{
        font-size:18px;
    }
    .auth-layout-left-content-form-title-wrapper h1{
        font-size:38px;
    }
    }

    @media screen and (max-width: 1089px) {
    .auth-layout-left-content-form-title-wrapper h3{
        font-size:18px;
    }
    .auth-layout-left-content-form-title-wrapper h1{
        font-size:35px;
    }
    }

    @media screen and (max-width: 1003px) {
    .auth-layout-left-content-form-title-wrapper h3{
        font-size:16px;
    }
    .auth-layout-left-content-form-title-wrapper h1{
        font-size:33px;
    }
    }
    @media screen and (max-width: 946px) {
    .auth-layout-left-content-form-title-wrapper h3{
        font-size:14px;
    }
    .auth-layout-left-content-form-title-wrapper h1{
        font-size:30px;
        /* line-height: 30px; */
    }
}